body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root .App{
  --fcc-flip-duration: 0.7s;
  --fcc-digit-block-width: 28px;
  --fcc-digit-block-height: 66px;
  --fcc-digit-font-size: 60px;
  --fcc-background: #003780;
  --fcc-label-color: #003780;
  --fcc-digit-color: #ffffff;
  --fcc-divider-color: none;
}
.center > div {
  margin: auto;
  /*! display: inline-flex; */
}
.center {
  display: flex;
  margin-top: 10%;
}
svg{
  height:auto;
  width:400px;
  margin-top: 60px;
}